import classnames from 'classnames'
import { Cell } from '../grid/Cell'
import { BaseModal } from './BaseModal'
import { MAX_WORD_LENGTH, MAX_CHALLENGES, PREFERRED_DISPLAY_LANGUAGE } from '../../constants/settings'
import { getStoredDisplayLanguage, getStoredIsHighContrastMode } from '../../lib/localStorage'

type Props = {
  isOpen: boolean
  handleClose: () => void
}

export const InfoModal = ({ isOpen, handleClose }: Props) => {
  const isHighContrast = getStoredIsHighContrastMode()
  const displayLanguage = getStoredDisplayLanguage()

  const arrowClassNames = classnames(
    {
      'text-orange-500': isHighContrast,
      'text-green-500': !isHighContrast,
    }
  )
  const headingClassNames = 'local-font text-sm font-bold text-gray-500 dark:text-gray-300 border'
  const rowClassNames = ''
  const cellClassNames = 'local-font text-sm text-gray-500 dark:text-gray-300 border align-top'

  if (displayLanguage === PREFERRED_DISPLAY_LANGUAGE) {
    return (
      <BaseModal title="" isOpen={isOpen} handleClose={handleClose}>
        <p className="text-left local-font text-sm text-gray-500 dark:text-gray-300">
          {MAX_CHALLENGES}{MAX_WORD_LENGTH}
          
        </p>

        <div className="flex justify-center mb-2 mt-2 md:mt-4">
          <Cell value="" status="correct" isRevealing={true} isCompleted={true} />
          <Cell value="" status="close" isRevealing={true} isCompleted={true} />
          <Cell value="" status="present" isRevealing={true} isCompleted={true} />
          <Cell value="" status="absent" isRevealing={false} isCompleted={true} />
        </div>
        <p className="local-font text-sm text-gray-500 dark:text-gray-300 pb-2">
          
        </p>
        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          <br />
          <br />
          <br />
          
        </p>
        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300">
          <span className={isHighContrast ? "text-violet-500" : "text-lime-500" }></span> 
        </p>

        <p className="local-font text-lg leading-6 font-medium text-gray-900 dark:text-gray-100 mt-2 md:mt-4 mb-2">
          
        </p>

        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300">
          
        </p>
        <div className="flex justify-center mb-2 mt-2 md:mt-4">
          <Cell value="" status="consonant" isRevealing={true} isCompleted={true} />
          <Cell value="" isRevealing={false} isCompleted={true} />
          <Cell value="" status="vowel" isRevealing={true} isCompleted={true} />
          <Cell value="" isRevealing={false} isCompleted={true} />
        </div>
        <p className="local-font text-sm text-gray-500 dark:text-gray-300 pb-2">
          
        </p>
        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          
        </p>
        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          <br />
        </p>

        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          
        </p>

        <div className="flex flex-col">
          <div className="overflow-x-auto sm:-mx-6 lg:-mx-8">
            <div className="py-2 inline-block min-w-full sm:px-6 lg:px-8">
              <div className="overflow-x-auto">
                <table className="min-w-full">
                  <thead className={rowClassNames}>
                    <tr>
                      <td colSpan={16} className={headingClassNames}><span className={arrowClassNames}></span>  <span className={arrowClassNames}></span></td>
                    </tr>
                  </thead>
                  <tbody>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                      <td rowSpan={5} className={headingClassNames}><span className={arrowClassNames}><br /></span><br /><br /><br /><br /><br /><span className={arrowClassNames}><br /></span></td>
                    </tr>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                    </tr>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                    </tr>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                    </tr>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>

        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          <br />
        </p>
        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          <br />
        </p>
        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300">
          
        </p>

        <p className="local-font text-lg leading-6 font-medium text-gray-900 dark:text-gray-100 mt-2 md:mt-4 mb-2">
          
        </p>

        <p className="local-font text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          
        </p>

        <p className="local-font text-sm font-bold text-gray-500 dark:text-gray-300">
          
        </p>
      </BaseModal>
    )
  } else {
    return (
      <BaseModal title="How to play" isOpen={isOpen} handleClose={handleClose}>
        <p className="text-left text-sm text-gray-500 dark:text-gray-300">
          Guess the word of the day in {MAX_CHALLENGES} tries. Each guess must be a valid {MAX_WORD_LENGTH}-kana word. Hit the enter button to submit.
          After each guess, the colour of the tiles will change to show how close your guess was to the word.
        </p>

        <div className="flex justify-center mb-2 mt-2 md:mt-4">
          <Cell value="" status="correct" isRevealing={true} isCompleted={true} />
          <Cell value="" status="close" isRevealing={true} isCompleted={true} />
          <Cell value="" status="present" isRevealing={true} isCompleted={true} />
          <Cell value="" status="absent" isRevealing={false} isCompleted={true} />
        </div>
        <p className="text-sm text-gray-500 dark:text-gray-300 pb-2">
          Answer: <span className="local-font"></span>
        </p>
        <p className="text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          The kana  is in the word and in the correct spot.<br />
          The kana  is close but incorrect. It is in the correct spot but the correct answer should be .<br />
          The kana  is in the word but in the wrong spot.<br />
          The kana  is not in the word in any spot.<br />
        </p>
        <p className="text-left text-sm text-gray-500 dark:text-gray-300">
          <span className={isHighContrast ? "local-font text-violet-500" : "local-font text-lime-500" }></span> indicates that it can be a basic kana, a voiced kana (), a semi-voiced kana (), or a small kana ().
        </p>

        <p className="local-font text-lg leading-6 font-medium text-gray-900 dark:text-gray-100 mt-2 md:mt-4 mb-2">
          Hint Mode
        </p>

        <p className="text-left text-sm text-gray-500 dark:text-gray-300">
          Two extra types of hints are available in Hint Mode.
        </p>
        <div className="flex justify-center mb-2 mt-2 md:mt-4">
          <Cell value="" status="consonant" isRevealing={true} isCompleted={true} />
          <Cell value="" isRevealing={false} isCompleted={true} />
          <Cell value="" status="vowel" isRevealing={true} isCompleted={true} />
          <Cell value="" isRevealing={false} isCompleted={true} />
        </div>
        <p className="text-sm text-gray-500 dark:text-gray-300 pb-2">
          Answer: <span className="local-font"></span>
        </p>
        <p className="text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          The kana  has up and down arrows in its tile. It shows that the correct answer is not  but another kana from the same COLUMN in the kana chart (i.e. ///).
        </p>
        <p className="text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          The kana  has left and right arrows in its tile. It shows that the correct answer is not  but another kana from the same ROW in the kana chart (i.e. //...).
        </p>

        <p className="text-left text-sm text-gray-500 dark:text-gray-300">
          This is the kana chart used in Hint Mode
        </p>

        <div className="flex flex-col">
          <div className="overflow-x-auto sm:-mx-6 lg:-mx-8">
            <div className="py-2 inline-block min-w-full sm:px-6 lg:px-8">
              <div className="overflow-x-auto">
                <table className="min-w-full">
                  <thead className={rowClassNames}>
                    <tr>
                      <td colSpan={16} className={headingClassNames}><span className={arrowClassNames}></span> COLUMN <span className={arrowClassNames}></span></td>
                    </tr>
                  </thead>
                  <tbody>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                      <td rowSpan={5} className={headingClassNames}><span className={arrowClassNames}><br /></span><br />R<br />O<br />W<br /><span className={arrowClassNames}><br /></span></td>
                    </tr>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                    </tr>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                    </tr>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                    </tr>
                    <tr className={rowClassNames}>
                      <td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}></td><td className={cellClassNames}><br /></td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>

        <p className="text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          Each COLUMN contains kana that have the matching consonant e.g. //// are in the same column with the same consonant K. Notice that basic kana, voiced kana (), semi-voiced kana () are seen as separate columns.
        </p>
        <p className="text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          Each ROW contains kana that have the matching vowel e.g. ///////////// are in the same row with the same vowel U.
        </p>
        <p className="text-left text-sm text-gray-500 dark:text-gray-300">
          Both the kana  and the long sound mark  do not have any matching rows or columns.
        </p>

        <p className="local-font text-lg leading-6 font-medium text-gray-900 dark:text-gray-100 mt-2 md:mt-4 mb-2">
          About the word list
        </p>

        <p className="text-left text-sm text-gray-500 dark:text-gray-300 pb-2">
          The word list includes nouns, pronouns, adjectives, adverbs and verbs. Foreign loanwords are also included. Conjugable words are in plain form (i.e. dictionary form).
        </p>

        <p className="text-sm font-bold text-gray-500 dark:text-gray-300">
          A new word will be available each day!<br />
        </p>
      </BaseModal>
    )
  }
}
